  --main-black: #000;
  --dark-black: #131313;
  --main-white: #fcfcfc;
  --simple-white: #fff;
  --yellow: #f8d24e;
  --bggreen: #84a178;
  --icecreambg: #aec6a5;
  --icecoffebg: #efd478;
  --milkshakebg: #d6936d;
  --inputbg: #f8d24e1a;
  --ingredients-green: #9cbe8f;
  --button-hover-focus: #0a8817;
  --button-disabled: #ababab;
  --S-text-size: 12px;
  --M-text-size: 14px;
  --L-text-size: 18px;
  --XL-text-size: 20px;
  --XXL-text-size: 30px;
  --font-wieght-XS: 400;
  --font-wieght-S: 500;
  --font-wieght-M: 600;
  --font-weight-L: 700;
  --font-wieght-XL: 800;
  --font-wieght-XXL: 900;
  --font-standard: "Montserrat", sans-serif;
  --font-special: "Seymour One", sans-serif;
}

body {
  font-size: 16px;
  font-family: var(--font-standard);
  background-color: var(--bggreen);
}

* {
  scroll-behavior: smooth;
