.checkbox {
  margin-bottom: 60px;
}

.last-checkbox {
  margin: 0;
}

.checkbox-group {
  width: 100%;
  max-width: 460px;
  -webkit-user-select: none;
  user-select: none;
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
  margin: 60px auto 40px;
  display: flex;
}

.checkbox-input {
  white-space: nowrap;
  display: none;
  position: absolute;
  overflow: hidden;
}

.checkbox-input:checked + .checkbox-tile-first {
  color: var(--ingredients-green);
}

.checkbox-input:checked + .checkbox-tile-second {
  color: var(--icecoffebg);
}

.checkbox-input:checked + .checkbox-tile-third {
  color: var(--milkshakebg);
}

.checkbox-input:checked + .checkbox-tile-first:before, .checkbox-input:checked + .checkbox-tile-second:before, .checkbox-input:checked + .checkbox-tile-third:before {
  opacity: 1;
  margin-top: 2px;
  transform: scale(1);
}

.checkbox-input:checked + .checkbox-tile-first:before {
  background-color: var(--icecreambg);
}

.checkbox-input:checked + .checkbox-tile-second:before {
  background-color: var(--icecoffebg);
}

.checkbox-input:checked + .checkbox-tile-third:before {
  background-color: var(--milkshakebg);
}

.checkbox-tile-first, .checkbox-tile-second, .checkbox-tile-third {
  width: 132px;
  height: 135px;
  cursor: pointer;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all .15s;
  display: flex;
  position: relative;
}

.checkbox-tile-first {
  border: 2px solid var(--icecreambg);
}

.checkbox-tile-second {
  border: 2px solid var(--icecoffebg);
}

.checkbox-tile-third {
  border: 2px solid var(--milkshakebg);
}

.checkbox-tile-first:before, .checkbox-tile-second:before, .checkbox-tile-third:before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: 0;
  background-repeat: no-repeat;
  border-radius: 14px;
  transition: all .25s;
  display: block;
  position: absolute;
  transform: scale(1);
}

.checkbox-tile-first:before {
  border: 2px solid var(--icecreambg);
}

.checkbox-tile-second:before {
  border: 2px solid var(--icecoffebg);
}

.checkbox-tile-third:before {
  border: 2px solid var(--milkshakebg);
}

.checkbox-tile-first:hover {
  background: var(--icecreambg);
}

.checkbox-tile-second:hover {
  background: var(--icecoffebg);
}

.checkbox-tile-third:hover {
  background: var(--milkshakebg);
}

.checkbox-label {
  color: #707070;
  text-align: center;
  transition: all .375s;
}

.product-img-first, .product-img-second, .product-img-third {
